body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
nav{
  padding: 0 1rem;
}

.content{
  min-height: calc(100vh - 70px - 64px);
  padding: 1.5rem 0;
}

.movies{
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap:1rem;
}
.goods{
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap:1rem;
  
}
.card-content{
  height: 90px;
}

.cart{
  position: fixed;
  bottom: 2rem;
  right: 2rem;

  cursor: pointer;
  z-index: 10;
  padding: 1rem;
  border-radius: 1rem;
  
}
.page-cart{
  position: absolute;
  top: 50%;
  left: 50%;

  background-color: brown;

}
@media(min-width: 767px) {
  .cart{
    top: 5rem;
    bottom: unset;
  }
 
  
}
@media(max-width: 767px) {
  
  #toast-container{
    width: 100%;
    left: 1rem;

  }
  
}

.cart-quantity{
  align-items: center;
  text-align: center;
}



.basket-list{
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  
  width: 50rem;
  max-width: 95%;

  animation: show 500ms ease-in-out;

  
}
.basket-close{
  cursor: pointer;

  position: absolute;
  top: 0.5rem;
  right: 0.5rem;

}
.basket__item-icon{
  cursor: pointer;

  
}
.basket__quantity {
  cursor: pointer;
  vertical-align:middle;
  color: rgb(23, 70, 223);
  margin: 0 0.4rem;

  
 
}


#toast-container{
  top:2rem !important;
  right: 9rem;


  animation: fade-in 250ms ease-in-out;
}


@keyframes fade-in {
  from {
    opacity: 0;
    transform: translateY(2rem);
  }
  to{
    opacity: 1;
    transform: translateY(0rem);
  }
}

@keyframes show {
  from{
    top: 70%;
    opacity: 0;

  }
  to {
    top: 50%;
    opacity: 1;
  }
}